.button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  transform: translate(0.25rem, 0.25rem);
  border: 0.0625rem solid #408f95;
  z-index: -2;
}

.button span {
